/* eslint-disable max-len */
import MasterdataRepository from './b8one.masterdataRepository';

import HttpClient from '../services/b8one.httpClient';

/**
 * AddressRepository
 */
class AddressRepository extends MasterdataRepository {
  /**
   * AddressRepository constructor
   */
  constructor() {
    super('AD');

    this._httpClient = new HttpClient();
    this._VTEX_API_ENDPOINT = '/api/dataentities/AD';
  }

  /**
   * This function return the user addresses stored in the masterdata by userEmail
   * @param  {String} userEmail
   * @param  {String|Array} fields=['id']
   */
  async getAllAddressesByUserEmail(userEmail, fields = ['id']) {
    return this._httpClient.get(
      `${this._VTEX_API_ENDPOINT}/search?_where=userId=${userEmail}&_fields=${fields}`
    );
  }

  /**
   * This function add a new document to Entity, the newAddress object need to have
   * the userID and addressName properties.
   * @param  {Object} newAddress
   */
  async addAddressToUser(newAddress) {
    return this._httpClient.post(
      `${this._VTEX_API_ENDPOINT}/documents`,
      newAddress
    );
  }

  /**
   * Remove a document of Address Entity by document ID
   * @param  {String} documentID
   */
  async removeAddressByDocumentID(documentID) {
    return this._httpClient.delete(
      `${this._VTEX_API_ENDPOINT}/documents/${documentID}`
    );
  }

  /**
   * Remove all document of Address Entity by userEmail
   * @param  {String} userEmail
   */
  async removeAllAddressesByUserEmail(userEmail) {
    try {
      const addresses = await this.getAllAddressesByUserEmail(userEmail);

      const addressesPromises = addresses.map(
        async address => await this.removeAddressByDocumentID(address.id)
      );

      return await Promise.all(addressesPromises);
    } catch (error) {
      console.error(error);
    }
  }
}

export default AddressRepository;
